@import 'test';

$color: #1245b7;

.autoprefixer-example {
  color: $color;
  opacity: 0.7;
  transition: 1s;
  display: flex;
  flex-direction: column;
  align-self: center;
  max-width: fit-content;
  padding: 10px 5px;
  border: 2px solid #d5640f;
  border-radius: 10px;
}

@supports not (display: grid) {
  div {
    float: right;
  }
}

@supports (display: flex) {
  @media screen and (min-width: 900px) {
    article {
      display: flex;
    }
  }
}

@layer base, special;

@layer special {
  .item {
    color: rebeccapurple;
  }
}

@layer base {
  .item {
    color: green;
  }
}